




































import {Vue, Prop, Component, } from 'vue-property-decorator'

@Component({
    name: 'Table'
})
export default class Table extends Vue {

    cities: object[] = [
        {
            name: 'Dijon',
            number: 50,
            editing_city: false,
            editing_number: false
        },
        {
            name: 'Belfort',
            number: 28,
            editing_city: false,
            editing_number: false
        }
    ];

    /** Editable Text Value  */
    value: string = 'Texte de base';

    /** Temp value  */
    tempValue: string = '';

    edit(index, column) {
        this.cities.forEach(function (el, id) {
                el['editing_city'] = false;
                el['editing_number'] = false;
            }
        );
        this.cities[index][column] = true;
    }


    get moyenne(){
        return (this.somme / this.cities.length);
    }
    get somme(){
        let total = 0;
        this.cities.forEach(function (el) {
            total += parseFloat(el['number']);
        });
        return total;
    }

    created() {
    }

    beforeMount() {
    }

    mounted() {
    }
}
